const ADD_ACCIDENT = 'ADD-ACCIDENT';
const DELETE_ACCIDENT = 'DELETE-ACCIDENT';
const FILTER_ARRAY = 'FILTER-ARRAY';


export const filterArray = (filterMachine) => {
    return {
        type: FILTER_ARRAY,
        filterMachine
    }
}

export const addAccidentToState = (objAccident) => {
    return {
        type: ADD_ACCIDENT,
        objAccident
    }
}

export const deleteAccident = (idAccident) => {
    return {
        type: DELETE_ACCIDENT,
        idAccident
    }
}


let initialState = {
    filterArrayMachine: [],
    list: [
    {id: 0, numberMachine: 58, part: "Двигатель напора", nameAccident: "Нет возбуждения правого двигателя напора", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" },
    {id: 1, numberMachine: 71, part: "Двигатель хода левый", nameAccident: "Нет хода от поворота", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" },
    {id: 2, numberMachine: 8, part: "Двигатель хода правый", nameAccident: "Нет хода от напора", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" },
    {id: 3, numberMachine: 70, part: "БУСМ", nameAccident: "Не работает дисплей у БУСМа", description: "bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla bla", date: "12.12.12" }
    ]
}
const accidentReducer = (state = initialState, action) => {
    switch (action.type) {

        case FILTER_ARRAY:
            return {
                ...state,
                filterArrayMachine: [...state.list.filter( accident => accident.numberMachine === action.filterMachine)]
            }

        case ADD_ACCIDENT:
            return {
                ...state,
                list: [...state.list, action.objAccident]
            }

        case DELETE_ACCIDENT:
            return {
                ...state,
                list: [...state.list.filter(elem => elem.id !== action.idAccident)]
            }

        default: return state;
    }
}

export default accidentReducer;