import React, {useEffect, useState} from "react";
import {Input, createField} from "../FormsControl/FormsControl";
import {reduxForm} from "redux-form";
import handleSubmit from "redux-form/lib/handleSubmit";



const MachineParameter = ({machineId, machineParameter, parameterName, updateParameter, setDataEditComponent}) => {
    const [editMode, setEditMode] = useState(false);
    const [machineParameterLocal, setMachineParameterLocal] = useState(machineParameter);
    let dateEditReducer;

    useEffect(() => {
        setMachineParameterLocal(machineParameter)
    }, [machineParameter]);
    let activateEditMode = () => {
        setEditMode(true)}
    let deactivateEditMode = () => {
        let newDateReducer = new Date();
        dateEditReducer = "Дата: " + newDateReducer.getDate() + "." + newDateReducer.getMonth()
            + "." + newDateReducer.getFullYear() + " Время: " + newDateReducer.getHours() +":"+ newDateReducer.getMinutes()+":"+ newDateReducer.getSeconds();
        updateParameter(Number(machineParameterLocal), machineId, parameterName, dateEditReducer)
        setDataEditComponent(dateEditReducer);
        dateEditReducer = '';
        setEditMode(false)}
    let onParameterChange = (e) => {
        setMachineParameterLocal(e.currentTarget.value)}

    const onSubmit = (data) => {
        alert(JSON.stringify(data));
    }
    return <td>
        <form onSubmit={onSubmit}>
        {!editMode && <td onClick={activateEditMode}>{machineParameterLocal}</td>}
        {editMode && <td>
            {createField("Parameter", [], Input, "parameter", {
                onParameterChange,
                deactivateEditMode,
                machineParameterLocal
            })}
        </td>}
        </form>
    </td>

}
// <td>
//     {!editMode && <td onClick={activateEditMode}>{machineParameterLocal}</td>}
//     {editMode && <td><input onChange={onParameterChange} autoFocus={true}
//                             onBlur={deactivateEditMode} value={machineParameterLocal} /></td>}
// </td>
export const MachineParameterChange = reduxForm({form: 'edit-machine'}) (MachineParameter)

export default MachineParameterChange;